<template>
    <div class="common-layout">
        <el-container>
            <el-header>
                <div style="margin-bottom: 20px;"></div> <!--  -->
                <div class="header-content">
                    <el-button type="info" @click="goBack" class="back-button">Back</el-button>
                    <span class="text-large font-600 mr-3 title">L2M2M1()</span>
                </div>
                <div style="margin-top: 20px;"></div> <!--  -->
            </el-header>
            <el-main>
                <div class="image-container">
                    <img id="mapAll" ref="mapAll" :src="imageSrc" usemap="#image-map"
                        style="width: 100%; height: 100%; object-fit: contain;" />
                    <map name="image-map" id="image-map">
                        <area v-for="hotspot in hotspots" :key="hotspot.id" :shape="hotspot.shape"
                            :coords="hotspot.coords" :href="hotspot.href" @click.prevent="navigateTo(hotspot.href)"
                            @mouseover="highlightHotspot(hotspot.id)" @mouseout="unhighlightHotspot(hotspot.id)" />
                    </map>
                </div>
                <div style="margin-top: 30px;"></div> <!--  -->
                <el-steps style="max-width: 600px" :active="active" finish-status="success" align-center>
                    <el-step title="Step 1" />
                    <el-step title="Step 2" />
                    <el-step title="Step 3" />
                </el-steps>
                <el-button class="next-button" style="margin-top: 12px" @click="next">Next step</el-button>
                <div v-html="compiledMarkdown" class="markdown-body"></div>
                <!--  Step 1  -->
                <div v-if="active >= 1" class="rounded-box">
                    <div class="box-content">
                        <p>Some Text</p>
                    </div>
                </div>
            </el-main>
        </el-container>
    </div>
</template>
<script>
import $ from 'jquery';
import 'imagemapster';
import MarkdownIt from 'markdown-it';
export default {
    data() {
        return {
            active: 0,
            imageSrc: '/pictures/level2/L2M2M1.png', // 
            hotspots: [
                {
                    id: '1',
                    shape: 'poly',
                    coords: '935,578,936,673,939,683,944,688,953,693,1260,693,1269,688,1275,681,1279,679,1280,575,1274,566,1267,559,953,557,938,564',
                    href: '/l2m2m2'
                },
                {
                    id: '1',
                    shape: 'poly',
                    coords: '19, 121, 219, 120, 231, 114, 237, 111, 242, 105, 242, 3, 237, -4, 233, -10, 224, -13, 21, -12, 4, -11, -1, 1, -1, 7, 2, 107, 9, 116',
                    href: '/next-level/2'
                },//
                // ...
            ],
            markdownText: 
`****

> sql

****

- query_string
    > sql

- parsetree_list
    > parsetree_listpostgreList

    \`\`\`C++
    typedef union ListCell
    {
    void     ptr_value;
    int     int_value;
    Oid     oid_value;
    } ListCell;
    
    typedef struct List
    {
    *   NodeTag   type;     /* T_List, T_IntList, or T_OidList /
    *   int     length;     /* number of elements currently present /
    *   int     max_length;   /* allocated length of elements[] */
    ListCell   elements;   / re-allocatable array of cells /
    *   /* We may allocate some cells along with the List header: /
    ListCell  initial_elements[FLEXIBLE_ARRAY_MEMBER];
    *   /* If elements == initial_elements, it's not a separate allocation */
    } List;
    \`\`\`

- Query
    > 

    \`\`\`C++
    typedef struct Query
    {
    NodeTag   type;
    CmdType   commandType;  /* select|insert|update|delete|merge|utility /
    *   QuerySource querySource;  /* where did I come from? /
    *   int     resultRelation; /* rtable index of target relation for
                    * INSERT/UPDATE/DELETE/MERGE; 0 for SELECT */
    List     rtable;     / list of range table entries */
    FromExpr   jointree;   / table join tree (FROM and WHERE clauses);
                    * also USING clause for MERGE */
    List     targetList;   / target list (of TargetEntry) */
    } Query;
    \`\`\`
            `,
            md: new MarkdownIt({
                html: false,        //  HTML 
                xhtmlOut: false,    //  XHTML 
                breaks: false,      //  <br> 
                linkify: true,      //  URL
                typographer: true,  // 
                validate: true      // 
            })
        };
    },
    computed: {
        // Markdown
        compiledMarkdown() {
            return this.md.render(this.markdownText);
        }
    },
    mounted() {
        //  mapster  resize: true
        $('#mapAll').mapster({
            fillColor: '1AC4F9',
            strokeColor: "FFFFFF",
            strokeWidth: 3,
            fillOpacity: 0.6,
            singleSelect: true,
        });
    },
    methods: {
        next() {
            if (this.active++ > 2) this.active = 0;
        },
        navigateTo(url) {
            this.$router.push(url); // Vue Router
        },
        highlightHotspot(id) {
            const $map = $('#mapAll');
            $map.mapster('highlight', true, id); // 
        },
        unhighlightHotspot(id) {
            const $map = $('#mapAll');
            $map.mapster('set', false, id); // 
        },
        goBack() {
            this.$router.go(-1)
        }
    },
};
</script>

