<template>
    <div class="common-layout">
        <el-container>
            <el-header>
                <div style="margin-bottom: 20px;"></div> <!--  -->
                <div class="header-content">
                    <el-button type="info" @click="goBack" class="back-button">Back</el-button>
                    <span class="text-large font-600 mr-3 title">L1M1</span>
                </div>
                <div style="margin-top: 20px;"></div> <!--  -->
            </el-header>
            <el-main>
                <div class="image-container">
                    <img id="mapAll" ref="mapAll" :src="imageSrc" usemap="#image-map"
                        style="width: 100%; height: 100%; object-fit: contain;" />
                </div>
                <div style="margin-top: 30px;"></div> <!--  -->
                <el-steps style="max-width: 600px" :active="active" finish-status="success" align-center>
                    <el-step title="Step 1" />
                    <el-step title="Step 2" />
                    <el-step title="Step 3" />
                </el-steps>
                <el-button class="next-button" style="margin-top: 12px" @click="next">Next step</el-button>
               <div v-html="compiledMarkdown" class="markdown-body"></div>
               <!--  Step 1  -->
                <div v-if="active >= 1" class="rounded-box">
                    <div class="box-content">
                        <p>Some Text</p>
                    </div>
                </div>
            </el-main>
        </el-container>
    </div>
</template>

<script>
import $ from 'jquery';
import 'imagemapster';
import MarkdownIt from 'markdown-it';

export default {
    data() {
        return {
            active: 0,
            imageSrc: '/pictures/level1/L1M1.png',
            markdownText: 
`****

>

****

- postmaster
    >  Postmaster  fork  Postgres 

- postgres
    > Postgres

- ReadCommand
    \`\`\`C++
    /* ----------------
     *                ReadCommand 
     *                 inBuf
     *                 EOF
     * ----------------
     */
    static int
    ReadCommand(StringInfo inBuf)
    {
            int                        result;

            if (whereToSendOutput == DestRemote)
                    result = SocketBackend(inBuf);
            else
                    result = InteractiveBackend(inBuf);
            return result;
    }
    \`\`\`

****

- 
    > /
            `,
            md: new MarkdownIt({
                html: false,        //  HTML 
                xhtmlOut: false,    //  XHTML 
                breaks: false,      //  <br> 
                linkify: true,      //  URL
                typographer: true,  // 
                validate: true      // 
            })
        };
    },
    mounted() {
        //  mapster  resize: true
        $('#mapAll').mapster({
            fillColor: '1AC4F9',
            strokeColor: "FFFFFF",
            strokeWidth: 3,
            fillOpacity: 0.6,
            singleSelect: true,
        });
    },
    computed: {
        // Markdown
        compiledMarkdown() {
            return this.md.render(this.markdownText);
        }
    },
    methods: {
        next() {
            if (this.active++ > 2) this.active = 0;
        },
        navigateTo(url) {
            this.$router.push(url); // Vue Router
        },
        highlightHotspot(id) {
            const $map = $('#mapAll');
            $map.mapster('highlight', true, id); // 
        },
        unhighlightHotspot(id) {
            const $map = $('#mapAll');
            $map.mapster('set', false, id); // 
        },
        goBack() {
            this.$router.go(-1)
        }
    },
};
</script>

<style scoped>
.common-layout {
    height: 100%;
    /*  */
}

.image-container {
    position: relative;
    width: 100%;
    height: 100%;
    /*  */
    overflow: hidden;
    /*  */
}

img {
    width: 100%;
    /*  */
    height: 100%;
    /*  */
    object-fit: contain;
    /*  */
}

.header-content {
    display: flex;
    align-items: center;
    /*  */
    justify-content: flex-start;
    gap: 10px;
    /*  */
}

.header-content .back-button {
    line-height: 40px;
    /*  */
    padding: 10px 25px;
    /*  */
    font-size: 20px;
    /*  */
    /*  */
}


.header-content .title {
    font-size: 40px;
    /*  */
    font-weight: bold;
    /*  */
    line-height: 40px;
    /*  */
}

.next-button {
    font-size: 22px;
    /*  */
    padding: 15px 30px;
    /*  */
    height: 60px;
    /*  */
    width: 200px;
    /*  */
    line-height: 60px;
    /*  */
    border-radius: 5px;
    /*  */
}
</style>