<template>
    <div class="common-layout">
        <el-container>
            <el-header>
                <div style="margin-bottom: 20px;"></div> <!--  -->
                <div class="header-content">
                    <el-button type="info" @click="goBack" class="back-button">Back</el-button>
                    <span class="text-large font-600 mr-3 title">L3M2M3M2()</span>
                </div>
                <div style="margin-top: 20px;"></div> <!--  -->
            </el-header>
            <el-main>
                <div class="image-container" align-center>
                    <img id="mapAll" ref="mapAll" :src="imageSrc" usemap="#image-map"
                        style="width: 100%; height: 100%; object-fit: contain;" />
                    <map name="image-map" id="image-map">
                        <area v-for="hotspot in hotspots" :key="hotspot.id" :shape="hotspot.shape"
                            :coords="hotspot.coords" :href="hotspot.href" @click.prevent="navigateTo(hotspot.href)"
                            @mouseover="highlightHotspot(hotspot.id)" @mouseout="unhighlightHotspot(hotspot.id)" />
                    </map>
                </div>
                <div style="margin-top: 30px;"></div> <!--  -->
                <el-steps style="max-width: 600px" :active="active" finish-status="success" align-center>
                    <el-step title="Step 1" />
                    <el-step title="Step 2" />
                    <el-step title="Step 3" />
                </el-steps>
                <el-button class="next-button" style="margin-top: 12px" @click="next">Next step</el-button>
                <div v-html="compiledMarkdown" class="markdown-body"></div>
                <!--  Step 1  -->
                <div v-if="active >= 1" class="rounded-box">
                    <div class="box-content">
                        <p>Some Text</p>
                    </div>
                </div>
            </el-main>
        </el-container>
    </div>
</template>

<script>
import $ from 'jquery';
import 'imagemapster';
import MarkdownIt from 'markdown-it';

export default {
    data() {
        return {
            active: 0,
            imageSrc: '/pictures/level3/L3M2M3M2.png', // 
            hotspots: [
                // ...
                {
                    id: '1',
                    shape: 'poly',
                    coords: '135,246,341,245,353,245,357,237,360,230,361,134,357,122,350,119,343,115,140,115,131,118,123,127,121,130,121,227,125,239',
                    href: '/l3m2m3m1'
                },
                {
                    id: '2',
                    shape: 'poly',
                    coords: '560,1726,766,1725,778,1725,782,1717,785,1710,786,1614,782,1602,775,1599,768,1595,565,1595,556,1598,548,1607,546,1610,546,1707,550,1719',
                    href: '/l3m2m3m3'
                },
                {
                    id: '3',
                    shape: 'poly',
                    coords: '865,1157,1278,1156,1290,1149,1294,1140,1295,1032,1289,1024,1279,1017,861,1017,845,1024,844,1034,845,1141,850,1151,858,1154',
                    href: '/l4m2m3m2m1'
                },
            ],
            markdownText: `****

> 

****

- PlannerInfo
- RelOptInfo 

> - INTERSECT/UNION/MINUS

> - 

> - cheapest
            `,
            md: new MarkdownIt({
                html: false,        //  HTML 
                xhtmlOut: false,    //  XHTML 
                breaks: false,      //  <br> 
                linkify: true,      //  URL
                typographer: true,  // 
                validate: true      // 
            })
        };
    },
    computed: {
        // Markdown
        compiledMarkdown() {
            return this.md.render(this.markdownText);
        }
    },
    methods: {
        next() {
            if (this.active++ > 2) this.active = 0;
        },
        navigateTo(url) {
            this.$router.push(url); // Vue Router
        },
        highlightHotspot(id) {
            const $map = $('#mapAll');
            $map.mapster('highlight', true, id); // 
        },
        unhighlightHotspot(id) {
            const $map = $('#mapAll');
            $map.mapster('set', false, id); // 
        },
        goBack() {
            this.$router.go(-1)
        }
    },
    mounted() {
        $('#mapAll').mapster({
            fillColor: '1AC4F9',
            strokeColor: "FFFFFF",
            strokeWidth: 3,
            fillOpacity: 0.6,
            singleSelect: true,
        });
    }
};
</script>

<style scoped>
.common-layout {
    height: 100%;
    /*  */
}

.image-container {
    position: relative;
    width: 50%;
    height: 50%;
    /*  */
    overflow: hidden;
    /*  */
}

img {
    width: 30%;
    /*  */
    height: 30%;
    /*  */
    object-fit: contain;
    /*  */
}

.rounded-box {
    position: absolute;
    top: 20%;
    /*  */
    left: 30%;
    /*  */
    width: 200px;
    /*  */
    height: 100px;
    /*  */
    background-color: rgba(255, 255, 255, 0.5);
    /*  */
    border: 2px solid black;
    /*  */
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.box-content p {
    margin: 0;
    font-size: 16px;
    color: black;
}

.header-content {
    display: flex;
    align-items: center;
    /*  */
    justify-content: flex-start;
    gap: 10px;
    /*  */
}

.header-content .back-button {
    line-height: 40px;
    /*  */
    padding: 10px 25px;
    /*  */
    font-size: 20px;
    /*  */
    /*  */
}


.header-content .title {
    font-size: 40px;
    /*  */
    font-weight: bold;
    /*  */
    line-height: 40px;
    /*  */
}

.next-button {
    font-size: 22px;
    /*  */
    padding: 15px 30px;
    /*  */
    height: 60px;
    /*  */
    width: 200px;
    /*  */
    line-height: 60px;
    /*  */
    border-radius: 5px;
    /*  */
}
</style>