<template>
    <div class="common-layout">
        <el-container>
            <el-header>
                <div style="margin-bottom: 20px;"></div> <!--  -->
                <div class="header-content">
                    <el-button type="info" @click="goBack" class="back-button">Back</el-button>
                    <span class="text-large font-600 mr-3 title">L3M3M1M1</span>
                </div>
                <div style="margin-top: 20px;"></div> <!--  -->
            </el-header>
            <el-main>
                <div class="image-container">
                    <img id="mapAll" ref="mapAll" :src="imageSrc" usemap="#image-map"
                        style="width: 100%; height: 100%; object-fit: contain;" />
                    <map name="image-map" id="image-map">
                        <area v-for="hotspot in hotspots" :key="hotspot.id" :shape="hotspot.shape"
                            :coords="hotspot.coords" :href="hotspot.href" @click.prevent="navigateTo(hotspot.href)"
                            @mouseover="highlightHotspot(hotspot.id)" @mouseout="unhighlightHotspot(hotspot.id)" />
                    </map>
                </div>
                <div style="margin-top: 30px;"></div> <!--  -->
                <el-steps style="max-width: 600px" :active="active" finish-status="success" align-center>
                    <el-step title="Step 1" />
                    <el-step title="Step 2" />
                    <el-step title="Step 3" />
                </el-steps>
                <el-button class="next-button" style="margin-top: 12px" @click="next">Next step</el-button>
                <div v-html="compiledMarkdown" class="markdown-body"></div>
                <!--  Step 1  -->
                <div v-if="active >= 1" class="rounded-box">
                    <div class="box-content">
                        <p>Some Text</p>
                    </div>
                </div>
            </el-main>
        </el-container>
    </div>
</template>

<script>
import $ from 'jquery';
import 'imagemapster';

export default {
    data() {
        return {
            active: 0,
            imageSrc: '/pictures/level3/L3M3M1M1.png', // 
            hotspots: [
                // ...
                {
                    id: '1',
                    shape: 'poly',
                    coords: '39,182,247,181,259,174,261,164,262,75,254,66,249,62,35,60,24,66,23,79,21,164,24,173,30,177',
                    href: '/l2m2m2'
                },
                {
                    id: '2',
                    shape: 'poly',
                    coords: '630,943,837,943,846,945,851,951,857,959,857,1055,853,1063,848,1069,842,1073,631,1073,627,1070,621,1064,618,1057,618,959,618,952,624,947',
                    href: '/l3m2m3m2'
                },
            ],
            markdownText: `
- btinsert
- bt_myscankey(BTScanKey)
- index_getattr
- _bt_searchbb
- _bt_getrootb
- _bt_moverightb
- _bt_compare
- _bt_binsrch
- _BTreeTupleGetDownLinkb
- _bt_findinsertlocb
- _bt_insertonpgPageAddItemmemcpyPageHeaderDatalinpItemIdDatalinp
- _bt_split_bt_findinsertloc_bt_insertonpg
- _bt_insert_parent`,
            // md: new MarkdownIt({
            //     html: false,        //  HTML 
            //     xhtmlOut: false,    //  XHTML 
            //     breaks: false,      //  <br> 
            //     linkify: true,      //  URL
            //     typographer: true,  // 
            //     validate: true      // 
            // })
        };
    },
    computed: {
        // Markdown
        compiledMarkdown() {
            return this.$md.render(this.markdownText);
        }
    },
    methods: {
        next() {
            if (this.active++ > 2) this.active = 0;
        },
        navigateTo(url) {
            this.$router.push(url); // Vue Router
        },
        highlightHotspot(id) {
            const $map = $('#mapAll');
            $map.mapster('highlight', true, id); // 
        },
        unhighlightHotspot(id) {
            const $map = $('#mapAll');
            $map.mapster('set', false, id); // 
        },
        goBack() {
            this.$router.go(-1)
        }
    },
    mounted() {
        $('#mapAll').mapster({
            fillColor: '1AC4F9',
            strokeColor: "FFFFFF",
            strokeWidth: 3,
            fillOpacity: 0.6,
            singleSelect: true,
        });
    }
};
</script>

<style scoped>
.common-layout {
    height: 100%;
    /*  */
}

.image-container {
    position: relative;
    width: 100%;
    height: 100%;
    /*  */
    overflow: hidden;
    /*  */
}

img {
    width: 100%;
    /*  */
    height: 100%;
    /*  */
    object-fit: contain;
    /*  */
}

.rounded-box {
    position: absolute;
    top: 20%;
    /*  */
    left: 30%;
    /*  */
    width: 200px;
    /*  */
    height: 100px;
    /*  */
    background-color: rgba(255, 255, 255, 0.5);
    /*  */
    border: 2px solid black;
    /*  */
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.box-content p {
    margin: 0;
    font-size: 16px;
    color: black;
}

.header-content {
    display: flex;
    align-items: center;
    /*  */
    justify-content: flex-start;
    gap: 10px;
    /*  */
}

.header-content .back-button {
    line-height: 40px;
    /*  */
    padding: 10px 25px;
    /*  */
    font-size: 20px;
    /*  */
    /*  */
}


.header-content .title {
    font-size: 40px;
    /*  */
    font-weight: bold;
    /*  */
    line-height: 40px;
    /*  */
}

.next-button {
    font-size: 22px;
    /*  */
    padding: 15px 30px;
    /*  */
    height: 60px;
    /*  */
    width: 200px;
    /*  */
    line-height: 60px;
    /*  */
    border-radius: 5px;
    /*  */
}
</style>