<template>
    <div class="common-layout">
        <el-container>
            <el-header>
                <div style="margin-bottom: 20px;"></div> <!--  -->
                <div class="header-content">
                    <el-button type="info" @click="goBack" class="back-button">Back</el-button>
                    <span class="text-large font-600 mr-3 title">L2M2M2()</span>
                </div>
                <div style="margin-top: 20px;"></div> <!--  -->
            </el-header>
            <el-main>
                <div class="image-container">
                    <img id="mapAll" ref="mapAll" :src="imageSrc" usemap="#image-map"
                        style="width: 100%; height: 100%; object-fit: contain;" />
                    <map name="image-map" id="image-map">
                        <area v-for="hotspot in hotspots" :key="hotspot.id" :shape="hotspot.shape"
                            :coords="hotspot.coords" :href="hotspot.href" @click.prevent="navigateTo(hotspot.href)"
                            @mouseover="highlightHotspot(hotspot.id)" @mouseout="unhighlightHotspot(hotspot.id)" />
                    </map>
                </div>
                <div style="margin-top: 30px;"></div> <!--  -->
                <el-steps style="max-width: 600px" :active="active" finish-status="success" align-center>
                    <el-step title="Step 1" />
                    <el-step title="Step 2" />
                    <el-step title="Step 3" />
                </el-steps>
                <el-button class="next-button" style="margin-top: 12px" @click="next">Next step</el-button>
                <div v-html="compiledMarkdown" class="markdown-body"></div>
                <!--  Step 1  -->
                <div v-if="active >= 1" class="rounded-box">
                    <div class="box-content">
                        <p>Some Text</p>
                    </div>
                </div>
            </el-main>
        </el-container>
    </div>
</template>

<script>
import $ from 'jquery';
import 'imagemapster';
import MarkdownIt from 'markdown-it';

export default {
    data() {
        return {
            active: 0,
            imageSrc: '/pictures/level2/L2M2M2.png', // 
            hotspots: [
                {
                    id: '1',
                    shape: 'poly',
                    coords: '9,103,188,102,199,98,204,94,205,80,204,16,201,7,195,4,192,1,16,1,7,3,4,6,1,19,1,90,3,97',
                    href: '/l2m2m1'
                },
                {
                    id: '2',
                    shape: 'poly',
                    coords: '675,720,854,719,865,715,870,711,871,697,870,633,867,624,861,621,858,618,682,618,673,620,670,623,667,636,667,707,669,714',
                    href: '/l2m2m3'
                },
                // ...
            ],
            markdownText: 
`****

> 

****

- \`Query\`

- List* \`querytree_list\` 0-N

****

- Query L2M2M1\

- querytree_list List*, Query*

-  \`utilitie\` (\`CREATE TABLE\`) \`utilitie\`

- (\`list_make1\`)\`utilitie\`\`Query\`

- CREATE RULE(PG) **SELECT/INSERT/UPDATE/DELETE** **INSTEAD/ALSO**
  
    - INSTEADALSO
    
    - SELECTINSTEAD
    
    - SELECTRIRPostQUELRETRIEVESELECT\`RETRIEVE-INSTEAD-RULE\`

- RIRINSERT/UPDATE/DELETECTE(\`Common\`**\` \`**\`Table\`**\` \`**\`Expressions\`  / )\`TargetList\`

- RIRSELECTRTE(\`Range Table\`)CTE

- \`Query\`\`canSetTag\`
            `,
            md: new MarkdownIt({
                html: false,        //  HTML 
                xhtmlOut: false,    //  XHTML 
                breaks: false,      //  <br> 
                linkify: true,      //  URL
                typographer: true,  // 
                validate: true      // 
            })
        };
    },
    mounted() {
        //  mapster  resize: true
        $('#mapAll').mapster({
            fillColor: '1AC4F9',
            strokeColor: "FFFFFF",
            strokeWidth: 3,
            fillOpacity: 0.6,
            singleSelect: true,
        });
    },
    computed: {
        // Markdown
        compiledMarkdown() {
            return this.md.render(this.markdownText);
        }
    },
    methods: {
        next() {
            if (this.active++ > 2) this.active = 0;
        },
        navigateTo(url) {
            this.$router.push(url); // Vue Router
        },
        highlightHotspot(id) {
            const $map = $('#mapAll');
            $map.mapster('highlight', true, id); // 
        },
        unhighlightHotspot(id) {
            const $map = $('#mapAll');
            $map.mapster('set', false, id); // 
        },
        goBack() {
            this.$router.go(-1)
        }
    },
};
</script>

<style scoped>
.common-layout {
    height: 100%;
    /*  */
}

.image-container {
    position: relative;
    width: 100%;
    height: 100%;
    /*  */
    overflow: hidden;
    /*  */
}

img {
    width: 100%;
    /*  */
    height: 100%;
    /*  */
    object-fit: contain;
    /*  */
}

.header-content {
    display: flex;
    align-items: center;
    /*  */
    justify-content: flex-start;
    gap: 10px;
    /*  */
}

.header-content .back-button {
    line-height: 40px;
    /*  */
    padding: 10px 25px;
    /*  */
    font-size: 20px;
    /*  */
    /*  */
}


.header-content .title {
    font-size: 40px;
    /*  */
    font-weight: bold;
    /*  */
    line-height: 40px;
    /*  */
}

.next-button {
    font-size: 22px;
    /*  */
    padding: 15px 30px;
    /*  */
    height: 60px;
    /*  */
    width: 200px;
    /*  */
    line-height: 60px;
    /*  */
    border-radius: 5px;
    /*  */
}
</style>