<template>
  <div id="app">
    <!--  -->
    <router-view></router-view>
  </div>
</template>

<script>
import 'github-markdown-css';
export default {
  name: 'App'
};
</script>

<style>
/*  */
html, body {
  height: 100%;
  font-family: 'Roboto', sans-serif; /*  */
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/*  */
body {
  background-color: #121212; /*  */
  color: #e0e0e0; /*  */
}

/*  */
#app {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: linear-gradient(135deg, #2a2a2a, #111111);
  color: #e0e0e0; /*  */
  padding: 15px;
}

/* canvas */
canvas {
  display: block;
  max-width: 100%;
  height: auto;
  margin: 20px auto;
}

/* Markdown  */
.markdown-body {
  box-sizing: border-box;
  width: 100%;
  max-width: 800px; /*  */
  margin: 0 auto;
  padding: 30px;
  color: #e0e0e0; /*  */
  background-color: #1e1e1e; /*  */
  font-size: 16px;
  line-height: 1.6; /*  */
  border-radius: 8px; /*  */
}

/*  */
@media (max-width: 767px) {
  .markdown-body {
    padding: 15px;
    font-size: 14px; /*  */
  }
}

/*  */
.markdown-body th,
.markdown-body td {
  padding: 10px;
  border: 1px solid #444; /*  */
}

.markdown-body table tr:nth-child(even) {
  background-color: #333 !important; /*  */
}

.markdown-body tr:nth-child(odd) {
  background-color: #1e1e1e; /*  */
}

.markdown-body table th {
  background-color: #444 !important; /*  */
  font-weight: bold;
  text-align: left; /*  */
}

/*  */
.markdown-body a {
  color: #4caf50; /*  */
  text-decoration: none; /*  */
}

.markdown-body a:hover {
  color: #81c784; /*  */
  text-decoration: underline; /*  */
}

/*  */
button {
  background-color: #4caf50; /*  */
  color: #fff;
  border: none;
  padding: 12px 24px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

button:hover {
  background-color: #45a049; /*  */
  transform: scale(1.05); /*  */
}

button:focus {
  outline: none;
  box-shadow: 0 0 5px #4caf50; /*  */
}

/*  */
input,
textarea {
  background-color: #333; /*  */
  color: #e0e0e0; /*  */
  border: 1px solid #444; /*  */
  padding: 12px;
  border-radius: 5px;
  font-size: 16px;
  width: 100%;
}

input:focus,
textarea:focus {
  outline: none;
  border-color: #81c784; /*  */
  box-shadow: 0 0 5px #81c784; /*  */
}

/*  */
@media (max-width: 767px) {
  #app {
    padding: 10px;
  }

  .markdown-body {
    padding: 20px;
  }
}
</style>